.header{
  font-family: fantasy;
  font-size: larger;
  color: brown;
  margin-top: 0px;
  margin-left: 20px;
  display: flex;
  min-width: 600px;
}
body{
  background-color: blanchedalmond;
}
.nav-bar{
  display: flex;
  margin-top:0px;
  color:orange;
  font:bold;
  padding-left: 10px;
}
.tab:hover{
  background-color: brown;
}
h2{
  color:rgb(221, 129, 9);
  text-decoration: none;
  font-weight: bold;
  background-size: 10px 10px;
  padding-left:20px;
  padding-right:20px;
}
.Home {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap:0;
  margin-top: 50px;
  margin-left: 10px;
}
.Home h1{
  font-family: 'Times New Roman', Times, serif;
  flex-shrink: 1;
}
.Home p{
  font-family: 'Times New Roman', Times, serif;
  font-size: large;
  flex-shrink: 1;
}
.Home img{
  max-width: 100%;
}
.Picture{
  display:grid;
  padding:20px;
  grid-template-columns: repeat(auto-fill, minmax(200px,auto));
  grid-gap:1rem; 
  margin:auto;
}
.Picture img{
  max-width:100%;
  transition:all 1s ease;
  cursor:pointer;
}
.Videos{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  position:relative;
  width:100%;
  bottom:0;
}
.Videos iframe{
  margin-top: 20px;
  max-width:100%;
}
.Videos .Video{
  padding:20px;
}
.Project{
  padding:30px;
  position:relative;
  bottom:0;
  width:100%;
}
.Project img{
  max-width: 100%;
}
.Project .p{
    display:flex;
    height:350px;
    padding:5px;
}
.Project .w{
  margin-top:auto;
  margin-bottom:auto;
  padding-left:20px;
}
.Project .i{
  max-width:200px;
  display:block;
  margin-top: auto;
  margin-bottom: auto;
}


/* message */
.Messages .body {
  font-family: 'Open Sans', sans-serif;
  background-color: #fff;
}

.Messages .header {
  color: #fff;
  background-color: #103D5D;
  padding: 33px 0 25px 15px;
}

.Messages .header h1 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;
  
}

.Messages .fa {
  font-size: 22px;
  transform: translateY(50deg);  
}

.Messages .header i {
  vertical-align: middle;
  margin-left: 12px;
}

.Messages .container {
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 30px;
}

.Messages .wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.Messages .add-item {
  border-radius: 2px;
  border-top: 3px solid #103D5D;
  margin-right: 50px;
  background-color: rgb(237, 237, 237);
  padding: 60px 18px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  margin-bottom: 34px;  
  width: 25%;
  margin-right: auto;
}

.Messages .display-item {
  width: 65%;
  margin-left: 50px;
}

.Messages .display-item button {
  margin-top: 20px;
  margin-left: auto;
  display: block;
}

.Messages .add-item form {
  padding-top: 50px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  overflow: hidden;
}

.Messages .input {
  font-size: 22px;
  color: #000;
  padding: 18px 22px;
  font-size: 16px;
  margin-bottom: 17px;
  border: 0;
  display: block;
  width: 100%;  
}

.Messages ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.Messages li {
  width: calc(50% - 50px);
  list-style-type: none;
  margin-right: 50px;
  background-color: rgb(237, 237, 237);
  color: #000;
  margin-bottom: 30px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
}

.Messages h2 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

.Messages li h3 {
  background-color: #103D5D;
  margin: 0;
  color: #fff;
  font-weight: 300;
  padding: 15px;
}

.Messages li p {
  padding: 15px;
}

.Messages form button {
  width: 100%;
  margin-top: 10px;
}

.Messages button {
  border-radius: 2px;
  min-width: 88px;
  background-color: #FA6900;
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 14px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 10px 6px;
}

.Movies{
  display:grid;
  padding:20px;
  grid-template-columns: repeat(auto-fill, minmax(200px,auto));
  grid-gap:1rem; 
  margin:auto;
}
.Movies img{
  max-width:100%;
  transition:all 1s ease;
  cursor:pointer;
  opacity: 1;
}
.Movies img:hover{
  opacity: 0.5;
}